.contact-form-main-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #612d91;
    padding: 5vh 5vw;
    gap: 3vh;
}

.contact-form-main-container h1{
    color: #fff;
    text-align: center;
}

.contact-form-main-container p{
    color: #fff;
    text-align: center;
    font-size: 1.75rem;
    font-weight: 100;
}

.contact-form-options{
    display: flex;
    flex-direction: row;
    gap: 3vw;
    margin: 2vh 0;
}

.contact-form-options-map{
    background-color: #fff;
    padding: 1.75vw 1.75vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in;
}

.contact-form-options-map:hover{
    background-color: #612d91;
    cursor: pointer;
}

.contact-form-options-phone{
    background-color: #fff;
    padding: 1.75vw 1.75vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in;
}

.contact-form-options-phone:hover{
    background-color: #612d91;
    cursor: pointer;
}

.contact-form-options-email{
    background-color: #fff;
    padding: 1.75vw 1.75vw;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in;
}

.contact-form-options-email:hover{
    cursor: pointer;
}

.contact-form-details{
    display: flex;
    flex-direction: column;
    border: 5px 5px 0 0;
    background-color: #fff;
    border-radius: 10px;
    max-width: 100vw;
}

.contact-form-details-content{
    display: flex;
    flex-direction: row;
    background-color: #fff;
    padding: 3vh 2.5vw;
    border-radius: 5px 5px 0 0;
    gap: 2vw;
}

.contact-form-details-content-box-one{
    display: flex;
    flex-direction: column;
}

.contact-form-details-content-box-one p{
    color: #000;
    font-size: larger;
    text-align: left;
}

.contact-form-details-content-box-one-name{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5vh;
    margin-bottom: 2vh;
}

.contact-form-details-content-box-one-name p{
    color: black;
    font-size: larger;
}

.contact-form-details-content-box-one-name-input{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #ebebf1;
    padding: 1vh 0.5vw;
    border-radius: 5px;
    transition: box-shadow 0.3s ease-in;
}

.contact-form-details-content-box-one-name-input input{
    border: none;
    outline: none;
    padding-left: 0.75vw;
    font-size: large;
}

.contact-form-details-content-box-one-name-input:focus-within{
    box-shadow: 0 0 0 1px rgba(97, 45, 145, 1)
}

.contact-form-details-content-box-one-mail{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5vh;
    margin-bottom: 2vh;
}

.contact-form-details-content-box-one-mail p{
    color: black;
    font-size: larger;
}

.contact-form-details-content-box-one-mail-input{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #ebebf1;
    padding: 1vh 0.5vw;
    border-radius: 5px;
    transition: box-shadow 0.3s ease-in;
}

.contact-form-details-content-box-one-mail-input input{
    border: none;
    outline: none;
    padding-left: 0.75vw;
    font-size: large;
}

.contact-form-details-content-box-one-mail-input:focus-within{
    box-shadow: 0 0 0 1px rgba(97, 45, 145, 1)
}

.contact-form-details-content-box-one-phone{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5vh;
    margin-bottom: 2vh;
}

.contact-form-details-content-box-one-phone p{
    color: black;
    font-size: larger;
}

.contact-form-details-content-box-one-phone-input{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border: 1px solid #ebebf1;
    padding: 1vh 0.5vw;
    border-radius: 5px;
    transition: box-shadow 0.3s ease-in;
}

.contact-form-details-content-box-one-phone-input input{
    border: none;
    outline: none;
    padding-left: 0.75vw;
    font-size: large;
    transition: border 2s ease-in;
}

.contact-form-details-content-box-one-phone-input:focus-within{
    box-shadow: 0 0 0 1px rgba(97, 45, 145, 1)
}

.services-option{
    display: flex;
    flex-direction: row;
}

.services-heading{
    background-color: #fff;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding-bottom: 2vh;
    padding-left: 2.5vw;
}

.services-heading p{
    font-size: larger;
    color: #000;
}

.services-category-options{
    display: flex;
    background-color: #fff;
    gap: 2vw;
    padding-bottom: 4vh;
    padding-left: 2.5vw;
    padding-right: 2.5vw;
}

.services-category-options-one{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2vh 1vw;
    background-color: #f9f9fb;
    border-radius: 5px;
    border: 1px solid transparent;
    text-align: center;
    flex: 1;
}

.services-category-options-one:hover{
    border: 1px solid #612d91;
    background-color: #612d91;
    color: #fff;
    cursor: pointer;
}

.services-category-options-two{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    padding: 2vh 1vw;
    background-color: #f9f9fb;
    border-radius: 5px;
    text-align: center;
    flex: 1;
}

.services-category-options-two:hover{
    border: 1px solid #612d91;
    cursor: pointer;
    background-color: #612d91;
    color: #fff;
}

.services-category-options-three{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    padding: 2vh 1vw;
    background-color: #f9f9fb;
    border-radius: 5px;
    text-align: center;
    flex: 1;
}

.services-category-options-three:hover{
    border: 1px solid #612d91;
    cursor: pointer;
    background-color: #612d91;
    color: #fff;
}

.services-category-options-four{
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    padding: 2vh 1vw;
    background-color: #f9f9fb;
    border-radius: 5px;
    text-align: center;
    flex: 1;
}

.services-category-options-four:hover{
    border: 1px solid #612d91;
    cursor: pointer;
    background-color: #612d91;
    color: #fff;
}

.send-message-button-div{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    padding-bottom: 3vh;
    border-radius: 0 0 5px 5px;
}

.send-message-button{
    padding: 1.75vh 2vw;
    background-color: #612d91;
    font-size: large;
    border-radius: 5px;
    color: #fff;
    border: none;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.contact-form-details-content-box-two{
    display: flex;
    flex-direction: column;
    gap: 0.5vh;
    width: 100%;
}

.contact-form-details-content-box-two textarea{
    height: 83%;
    border: 1px solid #ebebf1;
    outline: none;
    transition: box-shadow 0.3s ease-in;
    border-radius: 5px;
    padding: 1vh 0.5vw;
}

.contact-form-details-content-box-two textarea:focus{
    box-shadow: 0 0 0 1px rgba(97, 45, 145, 1)
}

.contact-form-details-content-box-two p{
    text-align: left;
    font-size: larger;
    color: #000;
}

.show-contact-number-button-container{
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 25vh;
    width: 25vw;
}

.show-contact-number-button-container-button{
    background-color: #612d91;
    display: flex;
    gap: 0.5vw;
    align-items: center;
    justify-content: center;
    color: #fff;
    width: 75%;
    height: 40%;
    cursor: pointer;
}

@media (max-width: 767px) {
    .contact-form-details-content{
        flex-direction: column;
    }

    .contact-form-details-content-box-one-name-input{
        width: 100%;
        justify-content: initial;
        padding-left: 2vw;
        padding-right: 2vw;
    }

    .contact-form-details-content-box-one-mail-input{
        width: 100%;
        justify-content: initial;
        padding-left: 2vw;
        padding-right: 2vw;
    }

    .contact-form-details-content-box-one-phone-input{
        width: 100%;
        justify-content: initial;
        padding-left: 2vw;
        padding-right: 2vw;
    }

    .services-category-options{
        flex-direction: column;
    }
}